<template>
  <div class="navbar">
    <div>
      <hamburger
        :is-active="sidebar.opened"
        class="hamburger-container"
        @toggleClick="toggleSideBar"
      />

      <breadcrumb class="breadcrumb-container" />
    </div>
    <div class="right-menu">
      <div class="version">
        <!-- 版本 -->
        <el-popover title="版本号" placement="right" trigger="hover">
          <div class="version-info">
            <p>
              <span>前端: </span>
              <el-link type="info" :underline="false">{{ webVersion }}</el-link>
            </p>
            <p>
              <span>后端: </span>
              <el-link type="info" :underline="false">{{
                serverVersion
              }}</el-link>
            </p>
            <p>
              <span>更新：</span
              ><el-link
                type="info"
                :underline="false"
                v-html="updateContent"
              ></el-link>
            </p>
          </div>
          <i slot="reference" class="el-icon-info"></i>
        </el-popover>
      </div>
      <el-dropdown class="avatar-container" trigger="click">
        <div class="avatar-wrapper">
          <img :src="avatar" class="user-avatar" />
          <span class="user-name">{{ name }}</span>
          <!-- <img :src="avatar+'?imageView2/1/w/80/h/80'" class="user-avatar"> -->
          <i class="el-icon-caret-bottom" />
        </div>
        <el-dropdown-menu slot="dropdown" class="dropdown-menu">
          <router-link to="/">
            <el-dropdown-item><span>首页</span></el-dropdown-item>
          </router-link>
          <el-dropdown-item divided>
            <span style="display: block" @click="logout">退出登录</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>

<script>
const webVersion = require("../../../package").version;
import { mapGetters } from "vuex";
import Breadcrumb from "@/components/Breadcrumb";
import Hamburger from "@/components/Hamburger";
import { getUserName } from "@/utils/auth";
import { getServerInfo } from "@/api/login.js";

export default {
  data() {
    return {
      avatar: require("@/assets/svg/user.svg"),
      name: getUserName(),
      webVersion: "未知", //前端版本号
      serverVersion: "未知", //后端版本号
      updateContent: window.globalConfig.updateContent, //更新内容
    };
  },
  components: {
    Breadcrumb,
    Hamburger,
  },
  computed: {
    ...mapGetters(["sidebar"]),
  },
  mounted() {
    let versionArr = webVersion.split(".");
    this.webVersion =
      versionArr[2].slice(0, 4) +
      "." +
      versionArr[2].slice(4, 6) +
      "." +
      versionArr[2].slice(6) +
      "_" +
      versionArr[0] +
      "." +
      versionArr[1];
    // 获取后台版本号
    getServerInfo().then((res) => {
      if (res.state) {
        this.serverVersion = res.data;
      }
    });
  },
  methods: {
    toggleSideBar() {
      this.$store.dispatch("app/toggleSideBar");
    },
    async logout() {
      await this.$store.dispatch("LogOut");
    },
  },
};
</script>

