<template>
  <section class="app-main">
    <transition name="fade-transform" mode="out-in">
      <router-view
        :key="key"
        :innerHeightMinusFilter="innerHeightMinusFilter"
        :innerHeight="innerHeight"
        class="router-view"
        ref="routerView"
        @watchFilterHeight="watchFilterHeight"
      />
    </transition>
  </section>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "AppMain",
  data() {
    return {
      innerHeight: window.innerHeight, //视屏高度
      innerHeightMinusFilter: 0, //视屏高度 - 头部筛选部分
      hiddenSidebarAndNavbar: window.globalConfig.hiddenSidebarAndNavbar, //是否隐藏左侧菜单栏和头部, 用于ignition
    };
  },
  computed: {
    key() {
      return this.$route.path;
    },
    ...mapGetters(["sidebar"]),
  },
  watch: {
    sidebar: {
      handler: function () {
        //  当左侧菜单栏发生改变时，做出响应
        setTimeout(() => {
          this.onResize();
        }, 300);
      },
      deep: true,
    },
  },
  mounted() {
    this.onResize();
    window.addEventListener("resize", this.onResize);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.onResize);
  },
  methods: {
    // 屏幕大小发生变化时调用
    onResize() {
      this.innerHeight = window.innerHeight;
      if (this.hiddenSidebarAndNavbar) this.innerHeight += 50; //子页面都会减去header的50, 因此隐藏情况下加50
      this.watchFilterHeight();
      this.$nextTick(() => {
        let view = this.$refs.routerView;
        if (view && view.onResize) view.onResize();
      });
    },
    // 切换页面时，重新获取视屏高度 - 头部筛选部分
    watchFilterHeight() {
      this.$nextTick(() => {
        let view = this.$refs.routerView;
        if (view && view.$refs.filterContainer) {
          // 头部有过滤部分，且没有管理不同子页面
          this.innerHeightMinusFilter =
            this.innerHeight - view.$refs.filterContainer.offsetHeight;
        } else if (view && view.$refs.childrenRouter) {
          let childrenRouter =
            view.$refs.childrenRouter[0] || view.$refs.childrenRouter;
          // 头部有过滤部分，且有管理不同子页面
          if (childrenRouter.$refs.filterContainer) {
            this.innerHeightMinusFilter =
              this.innerHeight -
              childrenRouter.$refs.filterContainer.offsetHeight;
          } else {
            this.innerHeightMinusFilter = this.innerHeight;
          }
        } else {
          // 没有头部
          this.innerHeightMinusFilter = this.innerHeight;
        }
      });
    },
  },
};
</script>

