import request from '@/utils/request'

// 获取用户列表
export function getUserList(data) {
    return request.post('/user/list', data)
}

// 删除用户
export function deleteUser(data) {
    return request.get('/user/delete', data)
}

// 更改用户启用状态
export function updateEnabledState(data) {
    return request.get('/user/enabled', data)
}

// 更改用户信息
export function updateUserInfo(data) {
    return request.post('/user/update', data)
}

// 新增用户
export function addUser(data) {
    return request.post('/user/add', data)
}

// 重置用户密码
export function resetUserPassWord(data) {
    return request.post('/user/resetUserPassWord', data)
}

// 重置个人密码
export function resetMyselfPassWord(data) {
    return request.post('/user/resetMyselfPassWord', data)
}

// 获取用户个人信息
export function getUserInfo(data) {
    return request.get('/user/getUserInfo',data)
}