import Vue from 'vue'
import App from './App'
import store from './store'

import 'normalize.css/normalize.css' // A modern alternative to CSS resets
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import "vue-awesome/icons"  //使用图标包
import Icon from 'vue-awesome/components/Icon'

Vue.component("v-icon", Icon);

// 引入拖拽组件
import VueDragResize from 'vue-drag-resize'

Vue.component('vue-drag-resize', VueDragResize);

import '@/styles/index.scss' // global css
import '@/icons' // icon

import router from './router'
import "./router/permission"  //路由守卫
import directive from './directive' //权限和拖动等指令
import "./utils"  //引入公共方法

import svgJS from "svg.js"; //引入svg和svg扩展功能插件
import 'svg.panzoom.js';
import 'svg.draggable.js';
import 'svg.resize.js';
import 'svg.select.js';
import './styles/svg.select.css'

Vue.prototype.$svg = svgJS; //放入vue的原型链

Vue.use(ElementUI)
Vue.use(directive)

Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App)
})
