import router from './index'
import store from '@/store'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import { getToken, removeAll, getUserId, getRoleType } from '@/utils/auth'
import { addRouter } from '@/utils/addRouter'
import { filterAsyncRouter } from '@/store/modules/permission'
import { getPermissionlist } from '@/api/account/role'
NProgress.configure({ showSpinner: false })
const whiteList = ['/login', '/panel'] //登录白名单

// 消除报错
import VueRouter from 'vue-router'
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location, onResolve, onReject) {
  if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
  return originalPush.call(this, location).catch(err => err)
}

router.beforeEach((to, from, next) => {
  NProgress.start()
  if (getToken()) {
    // 已登录且要跳转的页面是登录页
    if (to.path === '/login') {
      next({ path: '/' })
      NProgress.done()
    } else {
      if (!store.getters.isLoadMenus) {
        store.dispatch('GetInfo', getUserId()).then(() => {
          loadMenus(next, to, getRoleType())// 动态路由，拉取菜单
        }).catch(() => {
          store.dispatch('LogOut')
        })
      } else {
        next()
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next(`/login`)
      NProgress.done()
    }
  }
})

export const loadMenus = (next, to, id) => {
  if (getRoleType() === undefined) {
    removeAll()
    next('/notRole')
  } else {
    getPermissionlist({ roleId: id }).then((res) => {
      if (!res.state || !res.data || res.data.length === 0) return next()
      let routers = addRouter(res.data)
      const asyncRouter = filterAsyncRouter(routers)
      asyncRouter.push({ path: '*', redirect: '/404', name: 'notFind', hidden: true })  //必须放最后
      store.commit('setIsLoadMenus', true)  //已获取过动态路由
      router.addRoutes(asyncRouter) //将动态路由放入router中
      store.commit('setRoutes', asyncRouter)  //将动态路由加上固定路由，放到菜单栏中
      next({ ...to, replace: true })
    })
  }
}

router.afterEach(() => {
  NProgress.done()
})
