import {  getMapInfo, getMapLines } from '@/api/panel'
import { getDifferentStatesNum } from '@/api/task/taskList'
const panel = {
  state: {
    taskDone: 0,
    taskAll: 0,
    mapInfo: '',
    mapLines: '',
    mapSpots: '',
    disable_vehicle: '',
    onLineRobot: [],  //初始值应该为空数组
    deviceInfo: '1',
    workNum: 0,
    alarmNum: 0,
    sokitInfo: '',
    sokitCallBack: false,
    vMapId: '',
    workList: [],
    showRobotName: true,
    lastData: null,
    mouseWheel: '',
    domainId:0
  },

  mutations: {
    SOKIT_CALL_BACK: (state, sokitCallBack) => {
      state.sokitCallBack = sokitCallBack
    },
    MOUSE_WHEEL: (state, mouseWheel) => {
      state.mouseWheel = mouseWheel
    },
    LAST_DATA: (state, lastData) => {
      state.lastData = lastData
    },
    SHOW_ROBOT_NAME: (state, showRobotName) => {
      state.showRobotName = showRobotName
    },
    VMAP_ID: (state, vMapId) => {
      state.vMapId = vMapId
    },
    DOMAIN_ID: (state, id) => {
      state.domainId = id
    },
    SOKIT_INFO: (state, sokitInfo) => {
      state.sokitInfo = sokitInfo
    },
    TASK_DONE: (state, taskDone) => {
      state.taskDone = taskDone
    },
    TASK_ALL: (state, taskAll) => {
      state.taskAll = taskAll
    },


    MAP_SPOTS: (state, mapSpots) => {
      state.mapSpots = mapSpots
    },
    MAP_INFO: (state, mapInfo) => {
      state.mapInfo = mapInfo
    },
    MAP_LINES: (state, mapLines) => {
      state.mapLines = mapLines
    },
    ONLINEROBOZT: (state, onLineRobot) => {
      state.onLineRobot = onLineRobot
    },
    WORKNUM: (state, workNum) => {
      state.workNum = workNum
    },
    ALARMNUM: (state, alarmNum) => {
      state.alarmNum = alarmNum
    },
    ROBOTPOSITION: (state, robotPosition) => {
      state.robotPosition = robotPosition
    },
    DEVICEINFO: (state, deviceInfo) => {
      state.deviceInfo = deviceInfo
    },
    RESET: (state) => {
      state.onLineRobot = null
    },
    WORK_LIST: (state, workList) => {
      state.workList = workList
    },
  },

  actions: {
    // 地图信息
    DeviceInfo({ commit }, mapId) {
      return new Promise((resolve, reject) => {
        getMapInfo(mapId).then(response => {
          commit('MAP_INFO', response)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },

    // 路线信息
    MapLines({ commit }, mapId) {
      return new Promise((resolve, reject) => {
        getMapLines(mapId).then(response => {
          commit('MAP_LINES', response)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    TaskDone({ commit }) {
      return new Promise((resolve, reject) => {
        getDifferentStatesNum().then(response => {
          let finishNum = response.finishNum
          let allNum = parseInt(response.waitingNum) + parseInt(response.executingNum) + parseInt(response.errorNum) + parseInt(response.finishNum) + parseInt(response.cancleNum)
          commit('TASK_DONE', finishNum)
          commit('TASK_ALL', allNum)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    SAVE_ID({ commit }, obj) {
      // crhuang fix：当obj是个对象时，会出现NaN的显示bug；这是由于首次连接websocket时，系统返回的信息是个对象造成的
      if (!Array.isArray(obj)) {
        return
      }
      commit('ONLINEROBOZT', obj)
      var alarm = 0
      if (obj.length !== 0) {
        for (const i of obj) {
          if (i.warnId !== 0) {
            alarm++
          }
        }
      }
      commit('ALARMNUM', alarm)
      var work = 0
      if (obj.length !== 0) {
        for (const i of obj) {
          if (i.status !== '空闲') {
            work++
          }
        }
      }
      commit('WORKNUM', work)
    },
    RESET_ID({ commit }) {
      commit('ONLINEROBOZT', [])
    }
  }
}

export default panel
