import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

import Layout from '@/layout'

export const constantRoutes = [
  { path: '/login', component: () => import('@/views/login/index'), hidden: true },
  { path: '/404', component: () => import('@/views/404/404'), hidden: true },
  { path: '/notRole', component: () => import('@/views/404/notRole'), hidden: true },
  {
    path: '/mapDetails',
    component: Layout,
    hidden: true,
    meta: {
      title: '地图管理',
      icon: 'map'
    },
    children: [
      {
        path: ':id',
        name: 'mapDetails',
        hidden: true,
        component: () => import("@/views/map/detail"),
        meta: {
          title: '地图详情'
        }
      },
    ]
  },
  {
    path: '/deviceDetails',
    component: Layout,
    hidden: true,
    meta: {
      title: '机器人管理',
      icon: 'device'
    },
    children: [
      {
        path: ':id',
        name: 'deviceDetails',
        hidden: true,
        component: () => import("@/views/device/detail"),
        meta: {
          title: '设备详情'
        }
      },
    ]
  },

  {
    path: '/traffic',
    component: () => import("@/views/editor/index"),
    name:"trafficEditor",
    hidden: true,
    meta: {
      title: '交管编辑',
      icon: 'editor'
    }
  },
  {
    path: '/mapEditor',
    component: () => import("@/views/map/mapEditor"),
    name:"mapEditor",
    hidden: true,
    meta: {
      title: '地图编辑',
      icon: 'editor'
    }
  },

  {
    path: '/redirect',
    component: Layout,
    hidden: true,
    children: [
      {
        path: '/redirect/:path(.*)',
        component: () => import('@/views/404/redirect')
      }
    ]
  },
]

export default new Router({
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRoutes
})
