import axios from 'axios'
import { Message } from "element-ui";
import { getTokenCopy, removeAll } from '@/utils/auth'
import store from '@/store'
// const defaultSettings = window.globalConfig

const instance = axios.create({
  baseURL: process.env.VUE_APP_BASE_API,
  timeout: 10000,
  // headers: {
  //   "name": defaultSettings.name, "token": defaultSettings.token
  // }
});

export const request = {
  async get(url, data, responseType = "", contentType = "application/json;charset=UTF-8") {
    try {
      let param = {
        method: "get", url, params: data, responseType, headers: {
          "Content-Type": contentType
        }
      };
      let { data: res } = await instance(param)
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  },
  async post(url, data, responseType = "", contentType = "application/json;charset=UTF-8") {
    try {
      let params = {
        method: "post", url, data: data || {}, responseType, headers: {
          "Content-Type": contentType
        }
      };
      let { data: res } = await instance(params);
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  },
  async delete(url, data) {
    try {
      let params = {
        method: "delete", url, params: data
      };
      let { data: res } = await instance(params);
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  },
  async patch(url, data) {
    try {
      let params = {
        method: "patch", url, params: data
      };
      let { data: res } = await instance(params);
      return handleResData.success(res)
    } catch (err) {
      handleResData.error(err)
    }
  }
};

// 处理接口返回的数据
const handleResData = {
  success: (res) => {
    if (!res) {
      Message.error("接口请求错误, 请重试")
    } else {
      if (res && res.state === false) {
        // 获取单台小车在线信息时，不在线也不报错
        if (res.errMsg !== '设备不在线或不存在') Message.error(res.errMsg)
      }
      if (res && res.code !== undefined && res.code !== 0) Message.error(res.message)
    }
    return res
  },
  error: (err) => {
    if (err.response.status === 401) {
      // 当token不存在时，即表示已经请求过退出登录接口，则不再请求
      if (getTokenCopy()) {
        removeAll()
        Message.error('登录过期或权限不足')
        store.dispatch('LogOut')
      }
    } else {
      Message.error("Error" + err)
    }
  }
}

export default request
