import Cookies from 'js-cookie'

// const userToken = 'JSESSIONID'
const userToken = 'token'
const userTokenCopy = "tokenCopy"
const userName = "userName"
const userId = "userId"
const roleName = "roleName"
const roleType = "roleType"
const asyncRouter = "asyncRouter"

// token
export function getToken() {
  return Cookies.get(userToken)
}

export function setToken(token) {
  return Cookies.set(userToken, token)
}

export function setTokenCopy(token) {
  Cookies.set(userTokenCopy, token)
}

// token复制，用于判断是否已经退出
export function getTokenCopy() {
  return Cookies.get(userTokenCopy)
}

// 用户名
export function setUserName(name) {
  Cookies.set(userName, name)
}

export function getUserName() {
  return Cookies.get(userName)
}

// 用户id
export function setUserId(id) {
  Cookies.set(userId, id)
}

export function getUserId() {
  return Cookies.get(userId)
}

// 用户类型
export function setRoleType(type) {
  Cookies.set(roleType, type)
}

export function getRoleType() {
  return Cookies.get(roleType)
}

// 动态路由
export function setAsyncRouter(router) {
  Cookies.set(asyncRouter, router)
}

export function getAsyncRouter() {
  return Cookies.get(asyncRouter)
}

// 移除所有
export function removeAll() {
  Cookies.remove(userToken)
  Cookies.remove(userTokenCopy)
  Cookies.remove(userName)
  Cookies.remove(userId)
  Cookies.remove(roleName)
  Cookies.remove(roleType)
  Cookies.remove(asyncRouter)
}