import store from '@/store'
export function addRouter(routerlist) {
  let routers = [] //路由集合
  let permissions = [] //用户权限集合
  // routerlist.splice(0,1)
  routerlist.forEach((items, index) => {
    let itemFirstObj = items[0].menuFirstPo  //取出包含一级权限的对象，当前在子数组的第一个对象中
    let name = itemFirstObj.menuCode // 一级路由名称
    let namePath = `/${name}` // 一级路由地址
    let route = {
      path: index === 0 ? '/' : namePath,
      name,
      priority: itemFirstObj.priority,
      meta: {
        title: itemFirstObj.menuName,
        icon: name
      },
      component: 'Layout',
      hidden: false
    } //一级路由
    let routeChildren = []  //二级路由
    if (items[1] && items[1].menuSecondPo) {
      for (let i = 0; i < items.length; i++) {
        let item = items[i]
        permissions.push(item.permissionPo.permissionCode)
        if (item.menuSecondPo && item.menuSecondPo.menuCode === item.permissionPo.permissionCode) {
          let menuPo = item.permissionPo
          let childrenPath = menuPo.permissionCode.split("/")[1]
          let type = menuPo.permissionType
          let routeChild = {
            path: childrenPath,
            hidden: !(type === 0),
            meta: {
              keepAlive: false,
              title: menuPo.permissionName,
            },
            component: menuPo.permissionCode
          }
          routeChildren.push(routeChild)
        }
      }
    } else {
      for (let i = 0; i < items.length; i++) {
        let item = items[i]
        permissions.push(item.permissionPo.permissionCode)
      }
      let menuPo = items[0].permissionPo
      let childrenPath = "index"
      let type = menuPo.permissionType
      let routeChild = {
        path: childrenPath,
        hidden: !(type === 0),
        meta: {
          keepAlive: false,
          title: menuPo.permissionName,
        },
        component: menuPo.permissionCode + "/" + childrenPath
      }
      routeChildren.push(routeChild)
    }
    route.children = routeChildren
    routers.unshift(route)
  })
  routers.sort((a, b) => a.priority - b.priority)
  routers[0].redirect = routers[0].children[0].component
  routers[0].children[0].path = routers[0].children[0].component
  store.commit('setPermissions', permissions)  //将用户权限列表放入vuex中
  return routers
}
