          //   if (res.username) {
          //     getOrderTypes().then(res => {
          //       commit('ACTION_TYPE_LIST', res)
          //     })
          //     getDomainList().then(res => {
          //       commit('DOMAIN_TYPE_LIST', res)
          //       let arr = []
