import request from '@/utils/request'

// 获取调度任务列表
export function getTaskList(data) {
    return request.post('/task/list', data)
}

// 新增调度任务
export function addTask(data) {
    return request.post('/task/add', data)
}

// 取消调度任务
export function cancelTask({ id }) {
    return request.post("task/cancel/" + id)
}

// 批量取消调度任务
export function canceBatchlTasks(data) {
    return request.post("/task/batchCancel", data)
}

// 获取任务类型列表
export function getTaskActionList(data) {
    return request.get("/task/getType", data)
}

// 通过任务链id查任务 - 暂时没用到
export function getTaskListByChain(data) {
    return request.get("/task/getTaskChainByIds", data)
}

// 暂停调度任务
export function pauseTask(data) {
    return request.get("/task/pause", data)
}

// panel
export function getDifferentStatesNum(data) {
    return request.get("/task/getDifferentStatesNum", data)
}