import Vue from 'vue'
import moment from 'moment'

// 2021-01-01 00:00:00
Vue.filter('dateTime', function (value) {
  if (!value) {
    return '———'
  } else {
    return moment(value).format("YYYY-MM-DD HH:mm:ss")
  }

})

// 2021-01-01
Vue.filter('dataDay', function (value) {
  if (!value) {
    return '———'
  } else {
    return moment(value).format("YYYY-MM-DD")
  }
})

// 00:00:00
Vue.filter('hoursTime', function (value) {
  return moment(value).format('HH:mm:ss')
})

// 0天0小时0分0秒
Vue.filter('dateFormat', function (value) {
  if (!value) {
    return '———'
  } else {
    // 总秒数
    var second = Math.floor((new Date().getTime() - value) / 1000);
    if (second < 0) second = 0; //修复bug：占用时间为-1，应该是后台保存时间时，只保存秒数并四舍五入导致的
    // 天数
    var day = Math.floor(second / 3600 / 24);
    // 小时
    var hr = Math.floor(second / 3600 % 24);
    // 分钟
    var min = Math.floor(second / 60 % 60);
    // 秒
    var sec = Math.floor(second % 60);
    return (day > 0 ? day + "天" : "") + (hr > 0 ? hr + "小时" : "") + (min > 0 ? min + "分" : "") + sec + "秒";
  }
})

// 任务优先级
Vue.filter('priorityStatus', function(value) {
  if(value === 1){
    return '高'
  }else if(value === 0){
    return  '普通'
  }else if(value  === -1 ){
    return '低'
  }else if(value > 1){
    return '紧急'
  }
})