
import store from '@/store'

export default {
  inserted(el, binding) {
    let { value } = binding //获取指令中的值
    if (value) {
      let permissions = store.getters.permissions  //获取用户的权限列表
      let hasPermissions; //是否拥有权限
      // 如果传递回数组，则表示至少拥有数组中的一个权限，则显示
      if (Array.isArray(value)) {
        hasPermissions = value.some(e => {
          return permissions.includes(e)
        });
        if (!hasPermissions) {
          el.parentNode && el.parentNode.removeChild(el)
        }
      } else if (value[0] === "!") {
        // 加"!"则表示，如果拥有该权限，则不显示
        let hasPermissions = permissions.includes(value.slice(1,))
        // 若没有，则将移除该DOM节点
        if (hasPermissions) {
          el.parentNode && el.parentNode.removeChild(el)
        }
      } else {
        // 如果拥有该权限，则显示
        let hasPermissions = permissions.includes(value)
        // 若没有，则将移除该DOM节点
        if (!hasPermissions) {
          el.parentNode && el.parentNode.removeChild(el)
        }
      }
    } else {
      throw new Error('请设置操作权限')
    }
  }
}
